ces Provided by both CIS and Another Organization\",\"s2\":\"No Services Delivered\",\"s3\":\"Provided by CIS Staff\",\"s4\":\"Brokered Services Delivered by Another Organization\"},\"enrichment\":{\"s1\":\"Services Provided by both CIS and Another Organization\",\"s2\":\"No Services Delivered\",\"s3\":\"Provided by CIS Staff\",\"s4\":\"Brokered Services Delivered by Another Organization\"},\"family_engagement\":{\"s1\":\"Services Provided by both CIS and Another Organization\",\"s2\":\"No Services Delivered\",\"s3\":\"Provided by CIS Staff\",\"s4\":\"Brokered Services Delivered by Another Organization\"},\"life_skills\":{\"s1\":\"Services Provided by both CIS and Another Organization\",\"s2\":\"No Services Delivered\",\"s3\":\"Provided by CIS Staff\",\"s4\":\"Brokered Services Delivered by Another Organization\"},\"physical_fitness_health\":{\"s1\":\"Services Provided by both CIS and Another Organization\",\"s2\":\"No Services Delivered\",\"s3\":\"Provided by CIS Staff\",\"s4\":\"Brokered Services Delivered by Another Organization\"},\"prof_mental_health\":{\"s1\":\"Services Provided by both CIS and Another Organization\",\"s2\":\"No Services Delivered\",\"s3\":\"Provided by CIS Staff\",\"s4\":\"Brokered Services Delivered by Another Organization\"},\"location\":{\"Ur\":\"Urban\",\"R\":\"Rural\"}},\"core_services\":{\"aa\":\"Academic Assistance\",\"bn\":\"Basic Needs\",\"bi\":\"Behavioral Interventions\",\"ccp\":\"College and Career Preparation\",\"csl\":\"Community and Service Learning\",\"en\":\"Enrichment\",\"fe\":\"Family Engagement\",\"ls\":\"Life Skills/Social Development\",\"pmh\":\"Professional Mental Health\",\"pph\":\"Professional Physical Health\"},\"homepage\":{\"title\":\"CIS Search Tool\",\"summary\":\"\"},\"nav\":{\"colophon\":[\"org\",\"resources\",\"terms\",\"privacy\",\"contact\",\"admin\"],\"primary\":[\"affiliates\",\"programs\",\"partners\"]},\"partners\":{\"title\":\"Partners\",\"summary\":\"Partnerships are a key part of the CIS model. More importantly, strong partnerships ensure that we are delivering our model with fidelity. For years CIS has been partnering with other organizations to help reach the millions of students across our nation. All partners you find during your search on this site have been vetted by the national office partnership and evaluation team. Not finding what youre looking for or need help? Contact Ashlee Chapman at [chapmana@cisnet.org](mailto:chapmana@cisnet.org).\\n\",\"fm_search\":[{\"name\":\"Search Filters\",\"type\":\"fieldset\",\"fields\":{\"q\":{\"name\":\"q\",\"label\":\"Keywords\",\"widget\":\"text\"}}},{\"name\":\"Demographics\",\"type\":\"fieldset\",\"fields\":{\"grade\":{\"name\":\"grade\",\"label\":\"Grades\",\"fold\":true,\"widget\":\"multi\"},\"gender\":{\"name\":\"gender\",\"label\":\"Gender\",\"fold\":true,\"widget\":\"multi\"},\"services\":{\"name\":\"services\",\"label\":\"CIS Core Service Categories\",\"fold\":true,\"widget\":\"multi_icon\"}}},{\"name\":\"Partner Details\",\"type\":\"fieldset\",\"folded\":true,\"fields\":{\"use_in_network\":{\"name\":\"use_in_network\",\"label\":\"Use in Network\",\"fold\":true,\"widget\":\"single_null\"},\"free_of_cost\":{\"name\":\"free_of_cost\",\"label\":\"Free of Cost\",\"fold\":true,\"widget\":\"single_null\"},\"reach\":{\"name\":\"reach\",\"label\":\"Organizational Reach\",\"fold\":true,\"widget\":\"multi\"},\"setting\":{\"name\":\"setting\",\"label\":\"Setting\",\"fold\":true,\"widget\":\"multi\"},\"service_tiers\":{\"name\":\"service_tiers\",\"label\":\"Tiers of Service\",\"fold\":true,\"widget\":\"multi\"},\"evidence\":{\"name\":\"evidence\",\"label\":\"Tiers of Evidence\",\"fold\":true,\"widget\":\"multi\"}}}],\"fixtures\":[{\"name\":\"Share Our Strength\",\"contact\":{\"name\":\"Brie Doyle\",\"title\":\"Manager, National Partnerships\",\"email\":\"Bdoyle@strength.org\"},\"slug\":\"/partners/partners_detail\",\"url\":\"http://www.nokidhungry.org\",\"reach\":[\"n\"],\"service_tier\":[1],\"core_services\":[\"bn\"],\"mission\":\"No child should grow up hungry in America, but one in six children struggles  with hunger. Share Our Strengths No Kid Hungry campaign is ending child  hunger in America by ensuring all children get the healthy food they need,  every day.\\n\",\"org_history\":\"Vestibulum suscipit nulla quis orci. Mauris sollicitudin fermentum libero. Phasellus dolor. Donec  quam felis, ultricies nec, pellentesque eu, pretium quis, sem. Praesent blandit laoreet nibh.\\nInteger ante arcu, accumsan a, consectetuer eget, posuere ut, mauris. Pellentesque dapibus hendrerit  tortor. Aenean vulputate eleifend tellus. Pellentesque posuere. Sed fringilla mauris sit amet nibh.\\n\",\"overview\":\"Through conversations with school leadership and review of school improvement plans, the CIS affiliate, site coordinator and their school support team work to align goals that best meet the needs of the students, avoiding duplication and overlap of supports. When site coordinators actually begin providing supports, they collaborate with community partners and businesses to recruit volunteers for tutoring programs, build up clothing closets with support from local businesses or connect struggling families with much-needed medical attention  just to name a few.\\n\",\"grades\":[\"ec\",\"el\",\"ms\",\"hs\",\"ps\"],\"gender\":[\"m\",\"f\"],\"student_needs\":[\"bi\",\"att\",\"ac\",\"hr\",\"pfe\"],\"setting\":[\"sch\",\"hf\",\"co\",\"sp\"],\"network_use\":false,\"featured_network\":\"CIS of Northern VA\",\"free_cost\":false,\"cost_desc\":\"contact Ashlee Chapman @ chapmana@cisnet.org\",\"webinars\":[\"http://resources.communitiesinschools.org\"],\"presentations\":[\"http://resources.communitiesinschools.org\"],\"core_member\":true},{\"name\":\"iCouldBe\",\"contact\":{\"name\":\"Kate Schrauth\",\"title\":\"Executive Director\",\"email\":\"Kate@icouldbe.org\"},\"slug\":\"/partners/partners_detail\",\"url\":\"http://www.icouldbe.org\",\"reach\":[\"n\"],\"gender\":[\"m\",\"f\"],\"grades\":[\"ms\",\"hs\"],\"service_tier\":[2,3],\"core_services\":[\"aa\"],\"mission\":\"Provide at-risk middle and high school students with an online community of professional mentors, empowering teens  to stay in school, plan for future careers, and achieve in life.\\t\\n\",\"org_history\":\"Vestibulum suscipit nulla quis orci. Mauris sollicitudin fermentum libero. Phasellus dolor. Donec  quam felis, ultricies nec, pellentesque eu, pretium quis, sem. Praesent blandit laoreet nibh.\\nInteger ante arcu, accumsan a, consectetuer eget, posuere ut, mauris. Pellentesque dapibus hendrerit  tortor. Aenean vulputate eleifend tellus. Pellentesque posuere. Sed fringilla mauris sit amet nibh.\\n\",\"overview\":\"E-Mentoring at iCouldBe is a journey that starts with students in public high schools across the US who select their  own mentors, professionals working in careers that the students may want to pursue, and definitely want to learn more  about. iCouldBe is embedded into existing classrooms, where one class period per week, students work on e-mentoring  activities that focus on academic success strategies, career exploration, and post-secondary educational exploration  and planning.\\n\",\"student_needs\":[\"bi\",\"att\",\"do\",\"ccr\"],\"setting\":[\"sch\",\"hf\",\"co\"],\"network_use\":\"yes\",\"featured_network\":[\"CIS of Charlotte (Pilot)\",\"CIS of San Antonio (Pilot)\"],\"free_cost\":\"yes\",\"cost_desc\":\"$300/student per year\",\"evidence_tier\":[4],\"core_partner\":true},{\"name\":\"Find Your Calling (EMSI)\",\"contact\":{\"name\":\"Rob Sentz\",\"title\":\"CEO/Founder\",\"email\":\"rob@economicmodeling.com\"},\"slug\":\"/partners/partners_detail\",\"url\":\"http://www.findyourcalling.com/CIS\",\"grades\":[\"ms\",\"hs\",\"al\"],\"gender\":[\"m\",\"f\"],\"reach\":[\"n\"],\"service_tier\":[1,2,3],\"core_services\":[\"ccp\",\"aa\"],\"student_need\":[\"ccr\",\"pfe\"],\"setting\":[\"sch\",\"hf\",\"co\"],\"mission\":\"Find Your Calling helps answer the question, what do you want to do when you grow up? Our mission is to help you  (and your parents and teachers) find fantastic careers and the education you need to achieve your goals. Find Your  Calling does this through a simple but powerful combination of assessment and data. First, the assessment measures  your interests, then you explore detailed data on matching occupations and colleges that offer relevant programs.\\n\",\"org_history\":\"Vestibulum suscipit nulla quis orci. Mauris sollicitudin fermentum libero. Phasellus dolor. Donec  quam felis, ultricies nec, pellentesque eu, pretium quis, sem. Praesent blandit laoreet nibh.\\nInteger ante arcu, accumsan a, consectetuer eget, posuere ut, mauris. Pellentesque dapibus hendrerit  tortor. Aenean vulputate eleifend tellus. Pellentesque posuere. Sed fringilla mauris sit amet nibh.\\n\",\"overview\":\"Find Your Calling is an online platform/resource that asks students a series of questions to determine their  interests and then matches those interests to colleges, occupations or other avenues. This platform allows  students to view jobs outside of their 'comfort zone' and explore local (and non local) colleges/universities  at their finger tips.\\n\",\"network_use\":\"yes\",\"featured_network\":\"CIS of Big Country\",\"free_cost\":false,\"cost_desc\":null,\"evidence_tier\":[4],\"webinars\":[\"http://resources.communitiesinschools.org\"],\"core_member\":false}]},\"programs\":{\"title\":\"Programs\",\"fm_search\":[{\"name\":\"Search Filters\",\"type\":\"fieldset\",\"fields\":{\"q\":{\"name\":\"q\",\"label\":\"Keywords\",\"widget\":\"text\"}}},{\"name\":\"Demographics\",\"type\":\"fieldset\",\"fields\":{\"grade\":{\"name\":\"grade\",\"label\":\"Grades\",\"fold\":true,\"widget\":\"multi\"},\"gender\":{\"name\":\"gender\",\"label\":\"Gender\",\"fold\":true,\"widget\":\"multi\"},\"race\":{\"name\":\"race\",\"label\":\"Race / Ethnicity\",\"fold\":\"truee\",\"widget\":\"multi\"},\"need\":{\"name\":\"need\",\"label\":\"Targeted Student Needs\",\"fold\":true,\"widget\":\"multi\"},\"services\":{\"name\":\"services\",\"label\":\"CIS Core Service Categories\",\"fold\":true,\"widget\":\"multi_icon\"}}},{\"name\":\"Program Details\",\"type\":\"fieldset\",\"fields\":{\"use_in_network\":{\"name\":\"use_in_network\",\"label\":\"Used by CIS Network\",\"fold\":true,\"widget\":\"single_null\"},\"free_of_cost\":{\"name\":\"free_of_cost\",\"label\":\"Free of Cost\",\"fold\":true,\"widget\":\"single_null\"},\"service_tiers\":{\"name\":\"service_tiers\",\"label\":\"Tiers of Service\",\"fold\":true,\"widget\":\"multi\"},\"evidence\":{\"name\":\"evidence\",\"label\":\"Tiers of Evidence\",\"fold\":true,\"widget\":\"multi\"}}}],\"fixtures\":[{\"name\":\"XY-Zone\",\"slug\":\"/programs/programs_detail\",\"summary\":\"The XY-Zonesupports and guides adolescent males as they journey into manhood. It is a leadership development and peer support program that develops productive life skills in high school-age, at-risk males.\\t\\n\",\"description\":\"XY Zone is a program for adolescent males that focus on the five Rs: respect, responsibility, relationships, role modeling, and reaching out. The program includes goal setting peer accountability and ongoing opportunities for participants to practice their SEL skills.\\n\",\"grade\":[\"ms\",\"hs\"],\"gender\":[\"m\"],\"race\":[\"am\",\"as\",\"bl\",\"hi\",\"wh\",\"tw\"],\"reach\":[\"n\"],\"student_needs\":[\"bi\",\"att\",\"do\",\"sel\"],\"student_characteristics\":[\"na\"],\"service_tier\":[2],\"setting\":[\"sch\",\"co\"],\"network_use\":true,\"partner_org\":false,\"core_services\":[\"bi\",\"csl\",\"en\",\"ls\"],\"free_cost\":true,\"assessment\":false,\"evidence_tier\":5},{\"name\":\"NFL Play 60\",\"slug\":\"/programs/programs_detail\",\"summary\":\"NFL PLAY 60 is the National Football League's campaign to encourage kids to be active for 60 minutes a day in order to help reverse the trend of childhood obesity. It is a collaboration between the NFL and American Heart Association.\\n\",\"description\":\"Through lessons, videos, and interactive activities, NFL Play 60 Challenge encouarges students to get 60 minutes or more of physical activity. Resources can be implemented in-school, with a student's family etc. The resources are also academically aligned to National Physical Education Learning Standards, Next Generation Science Standards, and Common Core State Standards.\\n\",\"grade\":[\"ms\",\"hs\"],\"gender\":[\"m\",\"f\"],\"race\":[\"am\",\"as\",\"bl\",\"hi\",\"wh\",\"tw\"],\"reach\":[\"n\"],\"student_needs\":[\"bi\",\"att\",\"do\",\"sel\"],\"student_characteristics\":[\"na\"],\"service_tier\":[1,2],\"setting\":[\"sch\",\"hf\",\"as\"],\"network_use\":false,\"partner_org\":false,\"core_services\":[\"bi\",\"csl\",\"en\",\"ls\"],\"free_cost\":true,\"assessment\":false,\"evidence_tier\":4},{\"name\":\"Why Try\",\"slug\":\"/programs/programs_detail\",\"summary\":\"The WhyTry Program is a resilience education curriculum that provides simple, hands-on solutions for dropout prevention, violence prevention, truancy reduction, and increased academic success.\\n\",\"description\":\"Curriculum designed for K-12 that uses a multi-sensory approach using visual analogies, reinforced through customized hip hop music, video, learning activities, journal activities and other multimedia. Why Try teaches critical SEL principals and resiliency. The program is usually used with individual students and in small groups.  \\n\",\"grade\":[\"el\",\"ms\",\"hs\"],\"gender\":[\"m\",\"f\"],\"race\":[\"am\",\"as\",\"bl\",\"hi\",\"wh\",\"tw\"],\"reach\":[\"n\"],\"student_needs\":[\"bi\",\"att\",\"do\",\"sel\"],\"student_characteristics\":[\"frpl\"],\"service_tier\":[1,2,3],\"setting\":[\"sch\",\"as\"],\"network_use\":true,\"affiliates\":[\"CIS of Mid-America at Iowa\",\"CIS of Mid-America at NE Kansas\",\"CIS of Mid-America at SE Kansas\",\"CIS of Mid-America at SW Kansas\",\"CIS of Ottawa\",\"CIS of Caldwell County\",\"CIS of Charlotte-Mecklenburg\",\"CIS of the Charleston Area\",\"CIS of Spokane\"],\"partner_org\":false,\"core_services\":[\"bi\",\"ls\"],\"free_cost\":false,\"cost_desc\":\"Elementary and Secondary curriculums are bout $300 each, a full one-year online curriculum is $500.\\n\",\"assessment\":false,\"outcomes\":[\"SEL\",\"Decreased absences\",\"Behavior\"],\"evidence_tier\":2}]},\"routes\":{\"org\":{\"title\":\"CIS.org\",\"url\":\"https://www.communitiesinschools.org/\",\"published\":true,\"target\":\"_top\"},\"resources\":{\"title\":\"Resources\",\"url\":\"https://resources.communitiesinschools.org\",\"published\":true,\"target\":\"_blank\"},\"terms\":{\"title\":\"Terms of Service\",\"url\":\"https://www.communitiesinschools.org/terms/\",\"published\":true},\"privacy\":{\"title\":\"Privacy Policy\",\"url\":\"https://www.communitiesinschools.org/privacy-policy/\",\"published\":true},\"contact\":{\"title\":\"Contact Us\",\"url\":\"https://www.communitiesinschools.org/contact-us/\",\"published\":true},\"admin\":{\"title\":\"Admin\",\"url\":\"{% url 'admin:index' %}\",\"published\":true},\"partners\":{\"title\":\"Partners\",\"slug\":\"partner_list\",\"fixture_slug\":\"/partners/partner_list/\",\"published\":true,\"add_class\":\"rxn--partners\"},\"programs\":{\"title\":\"Programs\",\"slug\":\"program_list\",\"fixture_slug\":\"/programs/program_list/\",\"published\":true,\"add_class\":\"rxn--programs\"},\"affiliates\":{\"title\":\"Affiliates\",\"slug\":\"affiliate_list\",\"fixture_slug\":\"/affiliates/affiliate_list/\",\"published\":true,\"add_class\":\"rxn--affiliates\"}},\"site\":{\"title\":\"CIS Search Tool\",\"project\":{\"name\":\"CIS Search Tool\",\"url\":\"https://communitiesinschools.org\",\"code\":\"cis06\"},\"org\":{\"name\":\"Communities In Schools\",\"short_name\":\"CIS\",\"url\":\"https://communitiesinschools.org\",\"address\":{\"street_1\":\"2345 Crystal Drive\",\"street_2\":\"Suite 700\",\"city\":\"Arlington\",\"state\":\"VA\",\"zip\":22202},\"tel\":{\"href\":18002474543,\"base\":\"800-247-4543\",\"nice\":\"800 CIS 4KIDS\